.userPhoto {
    width: 130px;
    height: 120px;
}

.wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

.preloader {
    width: 200px;
    height: 200px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.preloader img {
    width: 100%;
    height: 100%;
}